nb-divider,
.nb-divider {
  display: block;
  height: 0;
  border-top: 1px solid #246199;
  border-bottom: 1px solid white;
}
.ui-slider {
  background: transparent;
  border: none;
  height: 0;
}
.ui-slider:before {
  display: block;
  margin: 2px auto;
  content: " ";
  background: #444444;
  border: none;
  border-radius: 2px;
  width: 100%;
  height: 4px;
  z-index: 2;
}
.ui-slider .ui-slider-range {
  display: none;
}
.ui-slider.ui-slider-vertical {
  width: 16px;
  height: 100px;
}
.ui-slider.ui-slider-vertical .ui-slider-handle {
  left: 0;
  height: 6px;
  width: 16px;
  margin-bottom: -3px;
}
.ui-slider .ui-slider-handle {
  border: 1px solid #666666;
  background: #e6e6e6;
  z-index: 2;
  position: absolute;
  border-radius: 2px;
  cursor: pointer;
}
.ui-slider .ui-slider-handle:hover {
  background-image: -moz-linear-gradient(top, white 0%, #f2f2f2 100%);
  background-image: -webkit-linear-gradient(top, white 0%, #f2f2f2 100%);
  background-image: -o-linear-gradient(top, white 0%, #f2f2f2 100%);
  background-image: -ms-linear-gradient(top, white 0%, #f2f2f2 100%);
  background-image: linear-gradient(to bottom, white 0%, #f2f2f2 100%);
  background-repeat: repeat-x;
  filter: progid:DXImageTransform.Microsoft.gradient(startColorstr='#ffffffff', endColorstr='#fff2f2f2', GradientType=0);
}
.ui-slider .ui-slider-handle.active,
.ui-slider .ui-slider-handle:active {
  background: #cccccc !important;
  border-color: #808080 !important;
  outline: none !important;
}
.ui-slider .ui-slider-handle:focus {
  background-image: -moz-linear-gradient(top, white 0%, #f2f2f2 100%);
  background-image: -webkit-linear-gradient(top, white 0%, #f2f2f2 100%);
  background-image: -o-linear-gradient(top, white 0%, #f2f2f2 100%);
  background-image: -ms-linear-gradient(top, white 0%, #f2f2f2 100%);
  background-image: linear-gradient(to bottom, white 0%, #f2f2f2 100%);
  background-repeat: repeat-x;
  filter: progid:DXImageTransform.Microsoft.gradient(startColorstr='#ffffffff', endColorstr='#fff2f2f2', GradientType=0);
  outline: #7ca0c2;
}
